// frontend/src/components/MetricsDashboard.tsx
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

// Define the interface for the props
interface MetricsProps {
  data: any[]; // This will come from useSocketMetrics()
}
interface MetricsProps {
  title: string;
  data: any[];
  dataKey: string;
  color: string;
}

export function MetricsDashboard({ title, data, dataKey, color }: MetricsProps) {
  return (
    <div className="bg-slate-900 p-4 rounded-xl border border-slate-800">
      <h3 className="text-slate-400 text-xs font-bold mb-3">{title}</h3>
      <div className="h-32 w-full">
        <ResponsiveContainer>
          <LineChart data={data}>
            <Line 
              type="monotone" 
              dataKey={dataKey} 
              stroke={color} 
              dot={false} 
              isAnimationActive={false} 
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}