"use client";

import { useState } from "react";
import { ChatInterface } from "./components/ChatInterface";
import { MetricsDashboard } from "./components/MetricsDashboard";
import { useSocketMetrics } from "./hooks/useSocketMetrics";

export default function Home() {
  const { metrics, isLive } = useSocketMetrics();
  
  // For the assessment, you can hardcode a default agent 
  // or add a state to switch between created agents.
  const [selectedAgent] = useState({
    id: "clp-123", // Example ID from your DB
    name: "Support Intelligence",
    model: "gpt-4",
    systemPrompt: "You are a helpful AI assistant."
  });

  return (
    <div className="flex min-h-screen bg-zinc-950 text-white font-sans">
      {/* 1. Main Content Area */}
      <main className="flex flex-1 flex-col p-6 gap-6">
        
        {/* Header Section */}
        <header className="flex items-center justify-between border-b border-zinc-800 pb-4">
          <div>
            <h1 className="text-2xl font-bold tracking-tight">{selectedAgent.name}</h1>
            <p className="text-zinc-400 text-sm">Active Model: {selectedAgent.model}</p>
          </div>
          <div className="flex items-center gap-2 bg-zinc-900 px-3 py-1.5 rounded-full border border-zinc-800">
            <span className={`h-2 w-2 rounded-full ${isLive ? 'bg-emerald-500 animate-pulse' : 'bg-red-500'}`} />
            <span className="text-xs font-medium uppercase tracking-wider text-zinc-300">
              {isLive ? 'System Live' : 'Connecting...'}
            </span>
          </div>
        </header>

        {/* 2. Grid Layout */}
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 flex-1">
          
          {/* Left Side: Chat Interface (Takes 3/4 space) */}
          <div className="lg:col-span-3 h-[75vh]">
            <ChatInterface selectedAgent={selectedAgent} />
          </div>

          {/* Right Side: Analytics Panel (Takes 1/4 space) */}
          <div className="lg:col-span-1 flex flex-col gap-4">
            <h3 className="text-sm font-semibold text-zinc-500 uppercase tracking-widest">Real-time Vitals</h3>
            
            <MetricsDashboard 
              title="Latency" 
              data={metrics.latency} 
              color="#10b981" 
              dataKey="value" 
            />
            
            <MetricsDashboard 
              title="Token Throughput" 
              data={metrics.tokens} 
              color="#3b82f6" 
              dataKey="value" 
            />

            <div className="mt-auto p-4 bg-zinc-900 rounded-xl border border-zinc-800">
              <p className="text-xs text-zinc-500">
                Agent ID: <code className="text-zinc-300">{selectedAgent.id}</code>
              </p>
            </div>
          </div>

        </div>
      </main>
    </div>
  );
}