import { useEffect, useState } from 'react';
import { socket } from '../lib/socket';
// frontend/src/hooks/useSocketMetrics.ts
export function useSocketMetrics() {
    const [metrics, setMetrics] = useState({
      latency: [] as any[],
      tokens: [] as any[],
      messages: [] as any[]
    });
    const [isLive, setIsLive] = useState(false);
  
    useEffect(() => {
      const onConnect = () => setIsLive(true);
      const onDisconnect = () => setIsLive(false);
  
      const onTelemetry = (newPoint: any) => {
        setMetrics((prev) => {
          // Use the 'type' field from the backend to decide which array to update
          const targetKey = newPoint.type as keyof typeof metrics; 
          
          if (!prev[targetKey]) return prev;
  
          return {
            ...prev,
            [targetKey]: [...prev[targetKey], newPoint].slice(-20)
          };
        });
      };
  
      socket.on('connect', onConnect);
      socket.on('disconnect', onDisconnect);
      socket.on('telemetry_update', onTelemetry);
  
      return () => {
        socket.off('connect', onConnect);
        socket.off('disconnect', onDisconnect);
        socket.off('telemetry_update', onTelemetry);
      };
    }, []);
  
    return { metrics, isLive };
  }