"use client";
import { useState, useEffect } from 'react';
import { api } from "../lib/api"; // Use your api helper

interface Message {
  role: 'user' | 'assistant' | 'system';
  content: string;
}

export function ChatInterface({ agentId }: { agentId: string }) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [agentName, setAgentName] = useState("Agent");

  // Optional: Fetch agent name to show in the placeholder
  useEffect(() => {
    fetch(`http://localhost:4000/api/agents`)
      .then(res => res.json())
      .then(agents => {
        const current = agents.find((a: any) => a.id === agentId);
        if (current) setAgentName(current.name);
      });
  }, [agentId]);

  const sendMessage = async () => {
    if (!input.trim()) return;

    const userMsg: Message = { role: 'user', content: input };
    setMessages(prev => [...prev, userMsg]);
    setInput('');
    setIsTyping(true);

    try {
      const res = await fetch('http://localhost:4000/api/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
          agentId: agentId, // Pass ID instead of config object
          message: input 
        })
      });

      const data = await res.json();
      const assistantMsg: Message = { role: 'assistant', content: data.content };
      setMessages(prev => [...prev, assistantMsg]);
    } catch (err) {
      console.error("Failed to send", err);
    } finally {
      setIsTyping(false);
    }
  };

  return (
    <div className="flex flex-col h-full bg-zinc-900 rounded-xl border border-zinc-800 overflow-hidden">
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((m, i) => (
          <div key={i} className={`flex ${m.role === 'user' ? 'justify-end' : 'justify-start'}`}>
            <div className={`max-w-[80%] p-3 rounded-2xl ${
              m.role === 'user' ? 'bg-blue-600 text-white' : 'bg-zinc-800 text-zinc-100 border border-zinc-700'
            }`}>
              {m.content}
            </div>
          </div>
        ))}
        {isTyping && <div className="text-xs text-zinc-500 animate-pulse px-2">Agent is thinking...</div>}
      </div>
      
      <div className="p-4 border-t border-zinc-800 bg-zinc-900/50 flex gap-2">
        <input 
          className="flex-1 bg-zinc-800 border border-zinc-700 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 text-white"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && sendMessage()}
          placeholder={`Message ${agentName}...`}
        />
        <button onClick={sendMessage} className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg font-medium transition-colors">
          Send
        </button>
      </div>
    </div>
  );
}